<template>
  <div style="height:100px">
    <a-button @click="open">打开内嵌网页</a-button>
    <a-button @click="down">关闭内嵌网页</a-button>
  </div>
      <iframe
        :src="src"
        :width="width"
        :height="height"
        :frameborder="frameborder"
        :scrolling="scrolling"
        v-if="flag"></iframe>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
  flag: boolean;
}
export default defineComponent({
  name: "inlinechired",
  props: {
      src:{
          type:String,
          required:true
      },
      width:{
          type:String,
          default:"100%"
      },
      height:{
          type:String,
          default:"100%"
      },
      frameborder:{
          type:String,
          default:"0"
      },
      scrolling:{
          type:String,
          default:"auto"
      },
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ flag: false });
    const open = () => {
      data.flag = true
    }
    const down = () => {
      data.flag = false
    }
    return {
      ...toRefs(data),
      open,down
    };
  },
});
</script>

<style scoped lang="scss"></style>
