
import {defineComponent, reactive, toRefs, SetupContext} from 'vue'
interface Data {
  visible: boolean
}

export default defineComponent({
  name: "ToInform",
  components: {},
  props: {
    // 显示数量
    count: {
      type: [Number, String],
      default: 0
    },
    // 菜单方向
    placement: {
      type: String,
      default: 'top'
    }
  },
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({
      visible: false
    })
    return {
      ...toRefs(data)
    }
  }
})
