
import {
  defineComponent,
  reactive,
  toRefs,
  SetupContext,
  onMounted,
} from "vue";
import qrcode from "qrcode";
interface Data {
  value: string;
  url: string;
}
export default defineComponent({
  name: "ToQrcode",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ value: "", url: "" });
    const create = () => {
      qrcode
        .toDataURL("I am a pony!")
        .then((url) => {
          console.log(url);
          data.url = url as string;
        })
        .catch((err) => {
          console.error(err);
        });
    };
    return {
      ...toRefs(data),
      create,
    };
  },
});
