<template>
  <button @click="send" :disabled="disabled">{{msg}}</button>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext, onMounted } from "vue";
interface Data {
  msg: string;
  msn: number;
  disabled: boolean;
}
export default defineComponent({
  name: "ToSendverificationcode",
  props: {
      time:{
          type:Number,
          default:10
      },
      text:{
         type:String,
         default:"发送验证码"
      },
      sendText:{
        type:String,
        default:"s后重新发送"
      },
      endText:{
         type:String,
        default:"重新发送"
      }
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" ,msn:0,disabled:false});
    const send = () => {
       const name =  setInterval(()=>{
            if(data.msn > 0){
                data.msg = `${data.msn--}${props.sendText}`
                data.disabled = true
            }else if(data.msn===0){
                data.msg = props.endText
                data.msn = props.time!
                 data.disabled = false
                clearInterval(name)
            }
        },1000)
    }
    onMounted(()=> {
      data.msn = props.time!
      data.msg = props.text!
    })
    return {
      ...toRefs(data),send
    };
  },
});
</script>

<style scoped lang="scss"></style>

