import {App,Component} from "vue"
import {AiComponents} from "@/types"
import previewpicture from "@/components/previewpicture/Previewpicture.vue"
import inline from "@/components/inline/Inline.vue"
import tendency from "@/components/tendency/Tendency.vue"
import bottomtool from "@/components/bottomtool/Bottomtool.vue"
import watermark from "@/components/watermark/Watermark.vue"
import sendverificationcode from "@/components/Sendverificationcode/Sendverificationcode.vue"
import inform from "@/components/inform/Inform.vue"
import QRcode from "@/components/QRcode/QRcode.vue"
import progressbar from "@/components/progressbar/Progressbar.vue"
import copytext from "@/components/copytext/Copytext.vue"
import edittext from "@/components/edittext/Edittext.vue"

interface Globalcomponent {
    install: (app: App) => void
}

const globalComponents: Component[] & AiComponents[] = []
globalComponents.push(previewpicture)  // 图片预览
globalComponents.push(inline)  // 内嵌网页
globalComponents.push(tendency)  // 趋势
globalComponents.push(bottomtool)  // 底部工具栏
globalComponents.push(watermark)  // 水印
globalComponents.push(sendverificationcode)  // 发送验证码
globalComponents.push(inform)  // 通知图标
globalComponents.push(QRcode)  // 二维码生成
globalComponents.push(progressbar)  // 进度条
globalComponents.push(copytext)  // 进度条
globalComponents.push(edittext)  // 可编辑文字
const aicomponent: Globalcomponent = {
    install(app) {
        globalComponents.map((item: Component & AiComponents) => {
            app.component(item.name!,item)
        })
    }
}
export default aicomponent