
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
  msg: string;
}
export default defineComponent({
  name: "tendencychired",
  props: {
      type:{
          type:String,
          default:'up'
      },
      isReserve:{
          type:Boolean,
          default:false
      }
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" });
    return {
      ...toRefs(data),
    };
  },
});
