<template>
  <div style="display:flex">
    <div v-for="(item, index) in pictures" :key="index">
      <img
        :src="item"
        alt=""
        style="width:200px;height:200px;margin-right:10px"
        @click="look(item,index)"
      />
    </div>        
      <div class="shade" v-if="flag" @click="outbrowse">        
              <div style="position: absolute;top:20px;right:20px" @click.stop="flag = false" v-if="masklayerclose===false&&pictures.length>1">
                <img src="@/assets/out.png" alt="" >
              </div>
              <div class="view">         
                    <div class="pict">
                        <img src="@/assets/previous.png" alt="" v-if="pictures.length>1" @click.stop="previous">
                        <img :src="msg" alt="" class="msg" :style="{width:widzize+'px',transform:`rotate(${size}deg)`,transition:'0.6s'}">
                        <img src="@/assets/next.png" alt="" v-if="pictures.length>1" @click.stop="next">
                    </div>
                    <div class="control">
                        <div @click.stop="amplification" class="bnt">
                            <img src="@/assets/amplification.png" alt="">
                        </div>
                        <div @click.stop="lessen" class="bnt">
                            <img src="@/assets/lessen.png" alt="">
                        </div>
                        <div @click.stop="clockwise" class="bnt">
                            <img src="@/assets/clockwise.png" alt="" style="width:30px;height:30px">
                        </div>
                        <div @click.stop="anticlockwise" class="bnt">
                            <img src="@/assets/anticlockwise.png" alt="" >
                        </div>
                    </div>
              </div>
      </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
  msg: string;
  flag: boolean;
  widzize:number;
  size:number;
  indexes:number;
}
export default defineComponent({
  name: "previewpicture",
  props: {
    pictures: {
      type: Array,
      required: true,
    },
    masklayerclose: {
      type: Boolean,
      default: false,
    },
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({
      msg: "",
      flag: false,
      widzize:200,
      size:0,
      indexes:0,
    });
    const look = (item: string,index: number) => {
      data.msg = item
      data.flag = !data.flag
      data.indexes = index
    }
    const outbrowse = () => {
      if(props.pictures.length === 1) data.flag = false
      console.log(props.masklayerclose)
    }
    const amplification = () => {
      if(data.widzize < 690){
        data.widzize = data.widzize+50
      }else{
        const a = data.widzize
        data.widzize = a
        alert("图片过大")
      }   
    }
    const lessen = () => {
      if(data.widzize > 80){
         data.widzize = data.widzize-50
      }else{
        const b = data.widzize
        data.widzize = b    
        alert("图片过小")
      }
     
    }
    const clockwise = () => {
      data.size = data.size+90
    }
    const anticlockwise = () => {
      data.size = data.size-90
    }
    const previous = () => {
      if(data.indexes === 0 ){
        data.indexes = props.pictures.length-1
      }else{
        data.indexes = data.indexes - 1
      }    
      data.msg = props.pictures[data.indexes] as string
    }
    const next = () => {
      if(data.indexes === props.pictures.length-1 ){
        data.indexes = 0
      }else{
        data.indexes = data.indexes + 1
        console.log(data.indexes, '')
      }
      data.msg = props.pictures[data.indexes] as string
      
    }
    return {
      ...toRefs(data),look,amplification,lessen,clockwise,anticlockwise,previous,next,outbrowse
    };
  },
});
</script>

<style scoped lang="scss">
.shade{
  width:100vw;
  height: 100vh;
  top:0;
  left: 0;
  position: fixed;
  background: rgba(0,0,0,0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}
.pict{
  width: 700px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.control{
  width: 250px;
  height: 50px;
  padding:8px 13px;
  background: rgba(144, 143, 143, 0.5);
  border-radius: 25px;
  align-items: center;
  position: absolute;
  top:70%;
  left: 44%;
  display: flex;
  justify-content: space-between;
    &>div{
      margin:0px 10px;
    }
}
.bnt{
transition: transform 0.25s ease;
width: 30px;height: 30px;

}
.bnt:hover{
transform: scale(1.2,1.4);
cursor: pointer;  
border: none;
}
</style>
