<template>
  <div style="display:flex;text-algin:center">
    <slot name="text"></slot>
    <div class="icon" :class="isReserve? 'down' : 'up'" v-if="type==='up'">
        <CaretUpOutlined />
    </div>
    <div class="icon" :class="isReserve? 'up' : 'down'" v-if="type==='down'">
        <CaretDownOutlined />
    </div>
    <slot></slot>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
  msg: string;
}
export default defineComponent({
  name: "tendencychired",
  props: {
      type:{
          type:String,
          default:'up'
      },
      isReserve:{
          type:Boolean,
          default:false
      }
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" });
    return {
      ...toRefs(data),
    };
  },
});
</script>

<style scoped lang="scss">
.icon {
    font-size: 16px;
    margin: 0 5px;
    height: 100%;
}

.up {
    color: #f5222d;
}

.down {
    color: #52c41a;
}
</style>
