
import { defineComponent, reactive, toRefs, SetupContext, onMounted, watch } from "vue";
import {usewatermark} from '@/hooks/usewatermark';
interface Data {
  msg: boolean | null;
}
export default defineComponent({
  name: "ToWatermark",
  props: {
      text:{
          type:String,
          default:"水印内容"
      }
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: null });
    const {watermark} = usewatermark()
    const setup = () => {
      data.msg = true
    }
    const down = () => {
      data.msg = false
    }
    //监听msg控制水印的显示
    watch(() => data.msg,(val) => {
      if(val){
        watermark(props.text)
      }else{
        watermark("")
      }
    })
    onMounted(()=>{
      console.log(data.msg)
    })
    return {
      ...toRefs(data),watermark,setup,down
    };
  },
});
