
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
import {useCopy} from "@/hooks/usecopytext"
interface Data {
  value: string;
}
export default defineComponent({
  name: "ToCopytext",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
     const {copy} = useCopy()
    const data: Data = reactive<Data>({ value: "" });
    const copyStr = () => {
        copy(data.value)
    }
    return {
      ...toRefs(data),copyStr,copy
    };
  },
});
