<template>
  <div class="box">
    <div style="display:flex;justify-content: space-between;width:100%">
      <div style="display:flex;align-items: center;">
        {{ text }}
      </div>
      <div>
        <button class="cancel" >取消</button>
        <button class="confirm" @click="confirm(text)">确定</button>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext,  } from "vue";
interface Data {
  msg: string;
}
export default defineComponent({
  name: "ToBottomtool",
  props: {
    text: {
      type: String,
      default: "这是一段文字",
    },
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" });
    const confirm = (val: string) => {
      console.log(val)
    }
    return {
      ...toRefs(data),confirm
    };
  },
});
</script>

<style scoped lang="scss">
.box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 80px;
  border: 1px solid rgb(217, 217, 217);
  padding: 20px;
  display: flex;
  align-items: center;
}
.cancel {
  padding: 5px 10px;
  background: white;
  outline:none;
  border: 1px solid rgb(217, 217, 217);
}
.cancel:hover{
   border: 1px solid  rgb(64, 169, 255);
    color:  rgb(64, 169, 255);
}
.confirm {
  padding: 5px 10px;
  background: rgb(64, 169, 255);
  color: white;
  border: 1px solid rgb(64, 169, 255);
  margin: 20px;
  outline:none;
}
</style>
