
import { defineComponent, reactive, toRefs, SetupContext,  } from "vue";
interface Data {
  msg: string;
}
export default defineComponent({
  name: "ToBottomtool",
  props: {
    text: {
      type: String,
      default: "这是一段文字",
    },
  },
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" });
    const confirm = (val: string) => {
      console.log(val)
    }
    return {
      ...toRefs(data),confirm
    };
  },
});
