<template>
  <div>
  <a-input style="width: 600px" placeholder="输入的内容会被复制" v-model:value="value"></a-input>
  <a-button style="margin-left: 10px;" @click="copyStr" type="danger">复制</a-button>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
import {useCopy} from "@/hooks/usecopytext"
interface Data {
  value: string;
}
export default defineComponent({
  name: "ToCopytext",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
     const {copy} = useCopy()
    const data: Data = reactive<Data>({ value: "" });
    const copyStr = () => {
        copy(data.value)
    }
    return {
      ...toRefs(data),copyStr,copy
    };
  },
});
</script>

<style scoped lang="scss"></style>
