<template>
  <div>
    <a-layout id="components-layout-demo-top-side">
      <a-layout>
        <a-layout-sider width="200" style="background: #fff">
          <a-menu
            mode="inline"
            :style="{ height: '100%', borderRight: 0 }"
            v-model:selectedKeys="selectedKeys"
          >
            <a-menu-item v-for="item in menu" :key="item.path" @click="routers(item.path)">
              <component :is="item.icon"></component>
              {{ item.name }}
            </a-menu-item>
          </a-menu>
        </a-layout-sider>
        <a-layout-content
          :style="{
            background: '#fff',
            padding: '24px',
            margin: 0,
            minHeight: '280px',
          }"
        >
          <router-view />
        </a-layout-content>
      </a-layout>
    </a-layout>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
import { useRoute, useRouter } from "vue-router";
interface Menu {
  icon: string;
  name: string;
  path: string;
}
interface Data {
  msg: string;
  menu: Menu[];
  selectedKeys: string[];
}
export default defineComponent({
  name: "",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const route = useRoute();
    const router = useRouter();
    const data: Data = reactive<Data>({
      msg: "",
      menu: [
        {
          icon: "ShareAltOutlined",
          name: "常用hooks",
          path: "/about",
        },
        {
          icon: "ZoomInOutlined",
          name: "图片预览",
          path: "/images",
        },
        {
          icon: "BarChartOutlined",
          name: "趋势",
          path: "/tendency",
        },
        {
          icon: "ToolOutlined",
          name: "底部栏工具",
          path: "/bottomtoll",
        },
        {
          icon: "BellOutlined",
          name: "通知图标",
          path: "/inform",
        },
        {
          icon: "FundOutlined",
          name: "内嵌网页",
          path: "/inline",
        },
        {
          icon: "FundOutlined",
          name: "发送验证码",
          path: "/Sendverificationcode",
        },
        {
          icon: "PaperClipOutlined",
          name: "进度条拓展",
          path: "/Progressbar",
        },
        {
          icon: "QrcodeOutlined",
          name: "生成二维码",
          path: "/QRcode",
        },
        {
          icon: "CopyrightCircleOutlined",
          name: "复制文本",
          path: "/copytext",
        },
        {
          icon: "BorderTopOutlined",
          name: " 生成水印",
          path: "/watermark",
        },
        {
          icon: "EditOutlined",
          name: "  可编辑文字",
          path: "/edittext",
        },
      ],
      selectedKeys: [route.path],
    });
    const routers = (path: string) =>{
        router.push(path)
    }
    return {
      ...toRefs(data),
      routers,
    };
  },
});
</script>

<style scoped lang="scss"></style>
